import React from "react";

import { Card, CardBody, Container, Row, Col } from "reactstrap";

import IndexNavbar from "components/Navbars/IndexNavbar.js";
import DarkFooter from "components/Footers/DarkFooter.js";
import { FaqUl } from "components/Common/EctlCommon.js";
import { HashLink } from 'react-router-hash-link';

function Guidelines() {
  const [theme] = React.useState(localStorage.getItem("theme") === "true");
  React.useEffect(() => {
    document.body.classList.add("index-page");
    document.body.classList.add("sidebar-collapse");
    document.documentElement.classList.remove("nav-open");
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;
    return function cleanup() {
      document.body.classList.remove("index-page");
      document.body.classList.remove("sidebar-collapse");
    };
  });
  return (
    <>
      <div
        className="sub-page-background"
        style={{
          backgroundImage: "url(" + require("assets/img/bg.jpg") + ")",
        }}
      >
        <div
          className="sub-page-background"
          style={
            theme
              ? { "background-color": "#000000AA" }
              : {
                  background:
                    "linear-gradient(0deg,rgba(44,44,44,.2),rgba(3,161,224,.6))",
                }
          }
        >
          <IndexNavbar isStatic={true} />
          <Container style={{ color: "#000" }}>
            <Col className="ml-auto mr-auto" md="8">
              <Card
                style={
                  theme ? { backgroundColor: "#36393F", color: "#FFFFFF" } : {}
                }
              >
                <CardBody>
                  <h3 id="faq-top">Tiering Guidelines &amp; FAQs</h3>
                  <Row>
                    <Col>
                      <ul>
                        <li><HashLink smooth to="/guidelines#the-tierlist">The Tierlist</HashLink></li>
                        <ul>
                          <li><HashLink smooth to="/guidelines#what-are-tiers">What are tiers?</HashLink></li>
                          <li><HashLink smooth to="/guidelines#the-tier-numbers">The Tier Numbers, Mason: What Do They Mean?</HashLink></li>
                          <li><HashLink smooth to="/guidelines#what-are-tags">What are tags?</HashLink></li>
                        </ul>
                        <li><HashLink smooth to="/guidelines#the-tier-decisions">The Tier Decisions</HashLink></li>
                        <ul>
                          <li><HashLink smooth to="/guidelines#what-is-considered">What do we consider when we tier?</HashLink></li>
                          <li><HashLink smooth to="/guidelines#what-is-not-considered">What do we <em>not</em> consider when we tier?</HashLink></li>
                          <li><HashLink smooth to="/guidelines#how-is-it-all-decided">How is it all decided?</HashLink></li>
                        </ul>
                        <li><HashLink smooth to="/guidelines#other-faq">Other Frequently Asked Questions</HashLink></li>
                        <ul>
                          <li><HashLink smooth to="/guidelines#faq-hull-types">Why is the tier list sorted by hull types?</HashLink></li>
                          <li><HashLink smooth to="/guidelines#faq-ordering-intratier">How are ships ordered within a tier?</HashLink></li>
                          <li><HashLink smooth to="/guidelines#faq-tn-tags">If tags are conditions, why are TN ships tagged?</HashLink></li>
                          <li><HashLink smooth to="/guidelines#faq-ark-royal">Why is Ark Royal placed where she is?</HashLink></li>
                          <li><HashLink smooth to="/guidelines#faq-why-is">Why is ship X placed in tier Y?</HashLink></li>
                        </ul>
                      </ul>
                      <ul>
                        <li>
                          <h4 id="the-tierlist">The Tierlist</h4>
                          <FaqUl>
                            <li>
                              <h5 id="what-are-tiers">What are tiers?</h5>
                              <p>
                                Tiers are categories used to indicate a
                                ship’s typical strength. While many units work
                                fine, some units are better than others, and
                                this list provides specific categories to make
                                it easier to explain how good a ship is.
                              </p>
                              <p>
                                Tiers higher up on the page are stronger. Tiers
                                lower down on the page are weaker.
                              </p>
                              <p>
                                Not every ship in the same tier will be
                                identical in strength. (After all, there’s more
                                than six or so different power levels that a ship
                                can have.) However, a ship will be stronger
                                than everyone in the tier below it, and weaker
                                than everyone in the tier above it.
                              </p>
                            </li>
                            <li>
                              <h5 id="the-tier-numbers">The Tier Numbers, Mason: What Do They Mean?</h5>
                              <p>
                                Tiers range from <strong>T0</strong> to <strong>T5</strong>,
                                and anything in this range is something we consider to have
                                some sort of reasonable utility and value. Having a numbered
                                tier at all means they’re alright. Azur Lane is
                                not a particularly difficult game, so ships in
                                every tier are going to be viable.
                              </p>
                              <p>
                                <strong>Tier EX</strong> is reserved for
                                unusually exceptional units who are truly head
                                and shoulders above the competition.
                              </p>
                              <p>
                                <strong>TN</strong> is the “everything else”
                                category. It’s every ship in the game that
                                didn’t make it into one of the tiers.
                              </p>
                            </li>
                            <li>
                              <h5 id="what-are-tags">What are tags?</h5>
                              <p>
                                Tags are <strong>conditions</strong> for a
                                ship’s tier. They are labels that indicate
                                situations required to perform at the tier
                                they’re in.
                              </p>
                              <p>
                                Tags are <em>not</em> role markers. A ship
                                missing a tag does not mean that she cannot
                                do something, but rather, that it is not
                                necessary for her to do it to perform at her
                                tier.
                              </p>
                              <p>
                                <b>Illustrative Example:</b> <i>Duke of York</i> is
                                in T1, but tagged as Boss and Support. This
                                means she’s only going to perform as a T1
                                battleship if she’s in a boss fleet, and only
                                if you’re taking advantage of her support. If
                                you take her to a mob fleet, or you aren’t
                                taking advanage of her debuffs, then she is not
                                going to perform as well as “Tier 1” implies
                                she should.
                              </p>
                              <p>
                                <b>Illustrative Example:</b> <i>Duca degli
                                Abruzzi</i> is not tagged as a Tank. She is
                                tagged for Mobs, and she is tanky enough that
                                she can main-tank mobs. However, being a tank
                                is not a condition for her tiering, so she does
                                not have the Tank tag.
                              </p>
                            </li>
                            <HashLink smooth to="/guidelines#faq-top">Back to Top</HashLink>
                          </FaqUl>
                        </li>
                        <li>
                          <h4 id="the-tier-decisions">The Tier Decisions</h4>
                          <FaqUl>
                            <li>
                              <h5 id="what-is-considered">What do we consider when we tier?</h5>
                              <p>
                                Ships are tiered primarily against content found
                                in campaign fights, although not against any
                                specific map or specific world in the campaign.
                              </p>
                              <p>
                                Ships are tiered on “Auto” mode.
                              </p>
                              <p>
                                Ships are tiered in their complete state. This
                                means they need to be fully enhanced, fully
                                skilled, fully limit broken, and at full
                                development status for research ships (and FS5,
                                if applicable).
                              </p>
                              <p>
                                Ships are tiered assuming they are properly
                                equipped. We don’t require that all ships have
                                absolute-best highest-rarity equipment, but
                                ships need to have proper equipment and need to
                                be comparably equipped to compare them.
                              </p>
                              <p>
                                Ships are tiered by their performance in full
                                fleets. We do not tier the fleets themselves,
                                but we only consider how well a ship performs
                                in a reasonably well-built six-ship fleet.
                              </p>
                              <p>
                                Ships are tiered assuming they are not misused.
                                We assume that ships are placed in the proper
                                position in their fleet and in an appropriate
                                composition, and taken to a fight that doesn’t
                                counter that ship’s strengths.
                              </p>
                              <p>
                                Ships are tiered assuming they’re similar level
                                to the content they’re facing. We don’t require
                                that all ships be Level 120, but we do consider
                                them over Level 100, and they need to be
                                appropriately leveled for the fights they’re
                                in.
                              </p>
                            </li>
                            <li>
                              <h5 id="what-is-not-considered">What do we <em>not</em> consider when we tier?</h5>
                              <p>
                                Cost is not considered at all, including oil
                                cost, and investment required.
                              </p>
                              <p>
                                Buffs from other ships are not taken into account by
                                default. If we do consider <em>specific</em> other
                                ships or types of ships in the fleet to be necessary,
                                it will be indicated with a Paired tag.
                              </p>
                              <p>
                                Anti-Air is not a major component of a ship’s
                                tier unless they have a particularly impressive
                                amount. If a ship’s AA output is large enough
                                to increase your flexibility when building a
                                fleet, and we consider this extra flexibility
                                to be a condition for her tiering, then we
                                indicate this with the Anti-Air tag.
                              </p>
                              <p>
                                We do not assume players run specific ships in
                                their fleet. We do not tier around one specific
                                fight or type of fight. We do not assume
                                players have access to all ships. Those sorts of
                                assumptions do not coincide with the goal of this list.
                              </p>
                              <p>
                                A ship’s solo performance or performance in
                                small fleets is not considered.
                              </p>
                              <p>
                                A ship’s manual-mode performance is not considered.
                              </p>
                              <p>
                                Exercises, or player-vs-player fights, are not considered.
                              </p>
                              <p>
                                We do not consider how ships of other hull types perform.
                                Ships are only tiered against members of their own hull type.
                              </p>
                            </li>
                            <li>
                              <h5 id="how-is-it-all-decided">How is it all decided?</h5>
                              <p>
                                Ship tiers are decided by the evaluators, a
                                group of players who analyze ship strength. At
                                the bottom of this page, you can see “Community
                                Contributors” which lists the evaluators.
                                Evaluators test ships in sorties to determine
                                how good they are and decide on how much to
                                value their various strengths and
                                weaknesses before coming to a consensus.
                                Sometimes a consensus cannot be reached, and a
                                vote is taken.
                              </p>
                              <p>
                                Some ships have strong and unique, but somewhat
                                niche abilities. In these cases, evaluators
                                come to a subjective consensus based on how much
                                those abilities are valued.
                              </p>
                            </li>
                            <HashLink smooth to="/guidelines#faq-top">Back to Top</HashLink>
                          </FaqUl>
                        </li>
                        <li>
                          <h4 id="other-faq">Other Frequently Asked Questions</h4>
                          <FaqUl>
                            <li>
                              <h5 id="faq-hull-types">Why is the tier list sorted by hull types?</h5>
                              <p>
                                The tier list is sorted by hull types for the
                                sake of readability, maintainability, and to keep
                                the spirit of a tier list being a power ranking of
                                sorts. We assume users either understand, or will
                                get their information elsewhere, about the use cases
                                and dynamics of the different hull types.
                              </p>
                              <p>
                                Tiering based on roles becomes difficult when
                                ships can perform multiple roles. Is <i>Jintsuu</i> a
                                support, or a damage-dealer? Is <i>Roon µ</i> a
                                tank, or a support? Tiering by hull is a pragmatic
                                solution to this, because it allows us to place
                                units into isolated categories in a way that is easy
                                for readers to digest. Which is the end goal of a
                                tier list anyway.
                              </p>
                            </li>
                            <li>
                              <h5 id="faq-ordering-intratier">How are ships ordered within a tier?</h5>
                              <p>
                                Ships on this site are sorted first by rarity,
                                and then alphabetically. We do not rank ships
                                within a tier.
                              </p>
                            </li>
                            <li>
                              <h5 id="faq-tn-tags">If tags are conditions, why are TN ships tagged?</h5>
                              <p>
                                Everything in TN is unconditionally TN, and there is nothing
                                below TN, so if we wanted to go for strict consistency then
                                nothing in TN should be tagged. We considered de-tagging
                                everything in TN for exactly this reason. Ultimately we
                                decided not to, because it amounted to removing information
                                without changing anything. We decided that doing so would
                                be unhelpful and would only serve to satisfy people being
                                pedantic. So, tags are conditions, except for TN.
                              </p>
                            </li>
                            <li>
                              <h5 id="faq-ark-royal">Why is Ark Royal placed where she is?</h5>
                              <p>
                                This question about Ark Royal is probably the most common question we get, and it boils down to the target audience of the tier list. We predominantly tier around progression and campaign content, rather than META fights / Arbiter Bosses / Stronghold+Abyssals, etc. Against those sorts of bosses, you care much less about how strong an individual ship is and much more about how well it works with your setup as a whole. This is much harder to evaluate, and we don’t tier team comps. In specific boss-nuke setups, Ark Royal sometimes is the best thing you can get, but in a more general sense she’s not quite Enterprise levels of good in her own right. This was the consensus that the Eval team reached upon deliberation and a vote.
                              </p>
                            </li>
                            <li>
                              <h5 id="faq-why-is">Why is ship X placed in tier Y?</h5>
                              <p>
                                <a href="https://discord.gg/dmrG9kXpxE">Join our discord</a> and ask us! We’d be happy to explain. Just, please do not join and then immediately tell us we’re wrong without even reading the guidelines on this page. That is only going to annoy us. Since you’re reading this, congratz! That said, people who have not read this page frequently criticize us, so, well, please don’t do that.
                              </p>
                            </li>
                            <HashLink smooth to="/guidelines#faq-top">Back to Top</HashLink>
                          </FaqUl>
                        </li>
                      </ul>
                    </Col>
                  </Row>
                </CardBody>
              </Card>
            </Col>
          </Container>
        </div>
      </div>
      <DarkFooter />
    </>
  );
}

export default Guidelines;
