import React, { Component } from "react";
import { Row, Col } from "reactstrap";

export default class Ship extends Component {
  render() {
    const shipName = this.props.shipName;
    const shipInfo = this.props.shipInfo;
    const isRetrofit = shipInfo.roles.flat().includes("RETRO");
    const RoleTypes = this.props.RoleTypes;
    const display = this.props.willDisplay;
    const theme = this.props.theme;
    const themeColor = theme ? "#CDCDCD" : "#676767";
    let shipImage = isRetrofit
      ? require("assets/img/ship_images/" + shipName + "kaiicon.png")
      : require("assets/img/ship_images/" + shipName + "icon.png");
    const shipJson = require("assets/json/ship/" + shipName + ".json");

    const getShipRarityClass = () => {
      let rarity =
        "rarity-" + shipJson.Rarity.toLowerCase().replace(/\s/g, "-");
      if (isRetrofit) {
        switch (rarity) {
          case "rarity-normal":
            rarity = "rarity-rare";
            break;
          case "rarity-rare":
            rarity = "rarity-elite";
            break;
          case "rarity-elite":
            rarity = "rarity-super-rare";
            break;
          case "rarity-super-rare":
            rarity = "rarity-ultra-rare";
            break;
          default:
            break;
        }
      }
      return rarity;
    };

    const openModal = () => {
      const shipImageBanner = isRetrofit
        ? require("assets/img/ship_images/" + shipName + "kaibanner.png")
        : require("assets/img/ship_images/" + shipName + "banner.png");
      const rarity = getShipRarityClass();
      this.props.openShipModal({
        shipName: shipName,
        shipImage: shipImageBanner,
        shipJson: shipJson,
        shipRarity: rarity,
      });
    };

    const getRolesMarkup = () => {
      let shipIconList = [];
      for (const rolegroup of shipInfo.roles){
        rolegroup.sort((a, b) => RoleTypes[a].sort_index - RoleTypes[b].sort_index);
        for (const role of rolegroup){
          shipIconList.push(
            <img
              alt="..."
              data-tip={RoleTypes[role].tip}
              style={{ maxHeight: "20px" }}
              src={require("assets/img/icons/" + RoleTypes[role].img)}
              key={rolegroup + '_' + role}
            />
          );
        }
        shipIconList.push(
          <span key="/" style={{"color": themeColor, "maxHeight": "20px", "fontWeight": "700"}}>&nbsp;/&nbsp;</span>
        );
      }
      shipIconList.pop();
      return shipIconList;
    };

    return (
      <Col xs="4" sm="4" md="2" style={{ display: display ? "block" : "none" }}>
        <Row style={{ minHeight: "20px" }}>
          <Col style={{ whiteSpace: "nowrap" }}>{getRolesMarkup()}</Col>
        </Row>
        <img
          alt="..."
          className={
            "rounded img-raised ship-rarity-padding " + getShipRarityClass()
          }
          src={shipImage}
          style={{ cursor: "pointer" }}
          onClick={() => openModal()}
        ></img>
        <p
          className="ship-name-display"
          style={{color: themeColor}}
        >
          {shipJson.Name}
        </p>
      </Col>
    );
  }
}
