import React, { Component } from "react";
import { Col, UncontrolledTooltip } from "reactstrap";

const he = require('he');

const EqItem = (props) => {
  const style = {
    'position': props.itemIndex > 0 ? 'absolute' : 'relative',
    'zIndex': 10000 - 1000*props.slotNumber + 100*props.index - (props.itemIndex > 0 ? props.itemIndex + 50 : 0),
    'width': '100%',
    'height': '100%',
    'top':  ((props.itemIndex > 0 ? 15.0 : 0) + '%'),
    'left': ((props.itemIndex > 0 ? 25.0 : 0) + '%'),
  };
  if (!props.cache.raised_info){
    props.cache.raised_info = {};
  }
  const slotNumber = props.slotNumber.toString();
  if (!props.cache.raised_info[slotNumber]){
    props.cache.raised_info[slotNumber] = {};
  }
  const index = props.index.toString();
  const slInfo = props.cache.raised_info[slotNumber];
  if (!slInfo[index]){
    slInfo[index] = {
      itemIndex: 0,
      tooltipFuncs: [],
    };
  }

  const [tooltipOpen, setTooltipOpen0] = React.useState(false);

  const info = slInfo[index];
  const setTooltipOpen = (open) => {
    info.tooltipOpen = open;
    setTooltipOpen0(open);
  };
  const raiseLock = () => {
    setTooltipOpen(false);
    info.tooltipLock = true;
    info.wikiLock = true;
  };
  const toggle = () => {
    if (info.tooltipLock){
      info.tooltipLock = false;
    } else {
      setTooltipOpen(!info.tooltipOpen);
    }
  };

  if (!props.cache.allTooltipFuncs){
    props.cache.allTooltipFuncs = [];
  }

  props.cache.allTooltipFuncs.push(setTooltipOpen);
  info.tooltipFuncs.push(setTooltipOpen);

  return (<>
    <div
      style={style}
      id={"_" + props.slotNumber + "_" + props.index + "_" + props.itemIndex}
      onClick={(e) => {
        e.preventDefault();
        e.stopPropagation();
        props.cache.raised_info[slotNumber][index].tooltipFuncs[props.cache.raised_info[slotNumber][index].itemIndex](true);
        if (e.target.parentNode.dataset.raiseOnClick !== "true"){
          info.wikiLock = false;
          info.tooltipLock = false;
          return;
        }
        props.cache.closeAllTooltips();
        raiseLock();
        props.raise(e.currentTarget.parentNode, props.index, props.cache, props.slotNumber);
      }}
      onMouseOut={(e) => setTooltipOpen(false)}
      onDoubleClick={(e) => {
        e.preventDefault();
        e.stopPropagation();
        if (e.target.parentNode.dataset.openWiki === "true" && !info.wikiLock){
          props.openGearInWiki(props.item.gear);
        }
      }}
      data-raise-on-click={props.itemIndex === 1 ? "true" : "false"}
      data-open-wiki={props.itemIndex === 0 ? "true" : "false"}
    >
      <img
        alt="..."
        className={
          "rounded eq-rarity-padding " +
          props.getRarityColor(props.item.gear) + (props.itemIndex === 0 ? " img-raised" : "")
        }
        src={require("../../assets/img/eq_images/" +
          props.item.gear +
          ".png")}
        style={{
          maxWidth: "128px",
          width: "100%",
          cursor: "pointer",
        }}
      />
      {props.itemCount > 1 ? (
        <div style={{
          'position': 'absolute',
          'bottom': 0,
          'right': 0,
        }}>
          <svg style={{'pointerEvents': 'none', 'width': '100%', 'height': '100%'}} viewBox="0 0 100 100">
            <text style={{
            'color': '#0F0F0F',
            'fontWeight': '500',
            'fontFamily': 'Cabin Condensed',
            'textAnchor': 'end',
            'fontSize': '1.0rem',
            'userSelect': 'none',
            'WebkitUserSelect': 'none',
          }} x="94" y="93">{(props.itemIndex+1) + "/" + (props.itemCount)}</text>
          </svg>
        </div>
      ) : (<></>)}
      <UncontrolledTooltip
        delay={0}
        placement="auto"
        className="popover-default"
        target={"_" + props.slotNumber + "_" + props.index + "_" + props.itemIndex}
        toggle={toggle}
        isOpen={tooltipOpen}
      >
        <h6>{props.getGearName(props.item.gear)}</h6>
        <hr />
        {props.item.tooltip}
        <hr />
        {props.getGearAcq(props.item.gear)}
      </UncontrolledTooltip>
    </div>
  </>);
};

export default class EquipmentColumn extends Component {

  constructor(props){
    super(props);
    this.getRarityColor = props.getRarityColor;
    this.slotNumber = props.slotNumber;
    this.eqSlot = props.state.eqData["slot_" + this.slotNumber];
    this.isRetrofit = props.state.isRetrofit;
    this.shipData = props.state.shipData;
    this.shipInfo = props.state.shipInfo;
    this.eqType = this.shipData["Eq" + this.slotNumber + "Type"];
    if (!this.eqType){
      this.eqType = "Auxiliary " + (this.slotNumber - 3);
    }
    this.cache = props.cache;
  }

  openGearInWiki(gear){
    try {
      const gearJson = require("../../assets/json/eq_json/" + gear + ".json");
      window.open(
        "https://azurlane.koumakan.jp/wiki/"
          + he.decode(gearJson.Name)
            .replace(/\s/g, "_")
          + (gearJson.disambiguator ? "#" + gearJson.disambiguator : ""),
        "_blank"
      );
    } catch (e) {
      alert("Unable to find the link for this gear! :(");
    }
  }

  getGearAcq(gear){
    try {
      const gearLoc = require("../../assets/json/eq_json/" + gear + ".json")
        .DropLocation;
      return he.decode(gearLoc)
        .replaceAll(/\[\[([^|]*?\|)*?([^|]*?)\]\]/g, "$2")
        .replaceAll(/<br\s*\/?>/g, "")
        .replaceAll(/25px/g, "points");
    } catch (e) {
      return "-";
    }
  }

  getGearName(gear){
    try {
      const asset = require("../../assets/json/eq_json/" + gear + ".json");
      const name = asset.ENName && asset.ENName.length ? asset.ENName : asset.Name;
      return he.decode(name);
    } catch (e) {
      return "-";
    }
  }

  raise(item, index, cache, slotNumber){

    slotNumber = slotNumber.toString();
    index = index.toString();
    const slInfo = cache.raised_info[slotNumber];
    const info = slInfo[index];

    const ci = info.itemIndex;
    const c = item.childNodes[ci];
    const ni = c.nextSibling ? ci + 1 : 0;
    const n = item.childNodes[ni];
    const di = n.nextSibling ? ni + 1 : 0;
    const d = item.childNodes[di];

    c.style.position = 'absolute';
    c.style.top = d.style.top;
    c.style.left = d.style.left;
    c.style.zIndex = 10000;
    c.firstChild.classList.remove('img-raised');
    c.dataset.openWiki = "false";
    d.style.position = 'absolute';
    d.style.top = n.style.top;
    d.style.left = n.style.left;
    d.style.zIndex = 10001;
    d.firstChild.classList.remove('img-raised');
    d.dataset.raiseOnClick = "true";
    n.style.position = 'relative';
    n.style.top = 0;
    n.style.left = 0;
    n.style.zIndex = 10002;
    n.firstChild.classList.add('img-raised');
    n.dataset.raiseOnClick = "false";
    n.dataset.openWiki = "true";
    info.itemIndex = ni;
  }

  render(){
    return (<>
      <h6
        style={{
          width: "100%",
          maxHeight: "15px",
          marginBottom: "45px",
        }}
      >
      {this.eqType ? this.eqType : <span>&nbsp;</span>}
      </h6>
      <h6 style={{ width: "100%" }}>
        {!this.eqType || /^Auxiliar/.exec(this.eqType)
          ? <span>&nbsp;</span> :
            ((this.isRetrofit && this.shipData['Eq' + this.slotNumber + 'BaseKai']) || this.shipData['Eq' + this.slotNumber + 'BaseMax']) + ' × ' +
            ((this.isRetrofit && this.shipData['Eq' + this.slotNumber + 'EffInitKai']) || this.shipData['Eq' + this.slotNumber + 'EffInitMax']) + ' eff'
        }
      </h6>
      <Col>{this.eqSlot.map((item, index) => {
        const itemArray = Array.isArray(item) ? item : [item];
        const eqItems = [];
        let itemIndex = 0;
        for (const itemEntry of itemArray){
          eqItems.push(<EqItem
            item={itemEntry}
            index={index}
            itemIndex={itemIndex}
            getRarityColor={this.getRarityColor}
            openGearInWiki={this.openGearInWiki}
            getGearName={this.getGearName}
            getGearAcq={this.getGearAcq}
            cache={this.cache}
            raise={this.raise}
            slotNumber={this.slotNumber}
            itemCount={itemArray.length}
            key={itemIndex}
          />);
          itemIndex++;
        }
        return (
          <div
            key={index}
            style={{'position': 'relative', 'zIndex': 10000}}
          >
            {eqItems}
          </div>
        );
      })}</Col>
    </>);
  }
}
