import React, { Component } from "react";

import { Row, Col, Label, Input } from "reactstrap";

import HullRoles from "../../assets/json/hullroles.json";

export default class FilterBar extends Component {
  constructor(props) {
    super(props);
    this.state = { theme: localStorage.getItem("theme") === "true" };
  }
  render() {
    const shipHull = this.props.shipHull;
    const RoleTypes = this.props.RoleTypes;
    const ShipTiers = this.props.ShipTiers;
    const shipFilters = this.props.shipFilters;
    const setShipFilters = this.props.setShipFilters;

    const getItemMarkup = () => {
      let filterItemList = [];
      for (let shipRole in RoleTypes){
        let roleIndex = shipRole.indexOf("_INVERSE");
        let checkRole;
        if (roleIndex >= 0) {
          checkRole = shipRole.substr(0, roleIndex);
        } else {
          checkRole = shipRole;
        }
        let useRole = false;
        if (!HullRoles[shipHull].includes(checkRole)){
          for (let tierArray of ShipTiers[shipHull]){
            for (let shipName of tierArray){
              const shipInfo = require("../../assets/json/ship_info/" + shipName + ".json");
              if (shipInfo.roles.flat().includes(checkRole)){
                useRole = true;
                break;
              }
            }
            // apparently labels are not allowed :weirdchamp:
            if (useRole){
              break;
            }
          }
        }
        if (useRole){
          filterItemList.push(
            <Col sm="6" md="2" key={shipRole}>
              <Label check>
                <Input
                  name={shipHull}
                  type="radio"
                  checked={shipFilters === shipRole}
                  onChange={() => {
                    setShipFilters(shipRole);
                  }}
                ></Input>
                <span
                  className="form-check-sign"
                  style={this.state.theme ? { color: "white" } : {}}
                >
                  {RoleTypes[shipRole].filter_label} &nbsp;
                </span>
                <img
                  alt="..."
                  data-tip={RoleTypes[shipRole].tip}
                  style={{ maxHeight: "20px" }}
                  src={require("assets/img/icons/" + RoleTypes[shipRole].img)}
                ></img>
              </Label>
            </Col>
          );
        }
      }
      return filterItemList;
    };

    return (
      <>
        <Row>
          {
            <Col sm="6" md="2">
              <Label check>
                <Input
                  name={shipHull}
                  type="radio"
                  checked={shipFilters === ""}
                  onChange={() => {
                    setShipFilters("");
                  }}
                ></Input>
                <span
                  className="form-check-sign"
                  style={this.state.theme ? { color: "white" } : {}}
                >
                  All Ships
                </span>
              </Label>
            </Col>
          }
          {getItemMarkup()}
        </Row>
      </>
    );
  }
}
