import React, { Component } from "react";
import FilterBar from "./FilterBar.js";
import Ship from "./Ship.js";
import { TabPane, Row } from "reactstrap";

export default class TierPane extends Component {
  componentDidUpdate() {
    this.props.ReactTooltip.rebuild();
  }
  constructor(props) {
    super(props);
    this.state = { theme: localStorage.getItem("theme") === "true" };
  }

  render() {
    const index = this.props.index;
    const ShipTiers = this.props.ShipTiers;
    const TierNames = this.props.TierNames;
    const RoleTypes = this.props.RoleTypes;
    const retrofitsList = this.props.retrofitsList;
    const shipHull = this.props.shipHull;
    const shipFilters = this.props.shipFilters;
    const setShipFilters = this.props.setShipFilters;
    const openShipModal = this.props.openShipModal;
    const willLoadPage = this.props.willLoadPage;

    return !willLoadPage ? (
      ""
    ) : (
      <TabPane tabId={"iconPills" + index}>
        <FilterBar
          RoleTypes={RoleTypes}
          ShipTiers={ShipTiers}
          shipHull={shipHull}
          shipFilters={shipFilters}
          setShipFilters={setShipFilters}
        />
        <hr />
        <h6
          style={
            this.state.theme
              ? {
                  color: "#CDCDCD",
                }
              : { color: "#676767" }
          }
        >
          Click on the ship to view more details!
        </h6>
        {ShipTiers[shipHull].map((tierArray, idx) => {
          if (tierArray.length !== 0) {
            return (
              <div key={idx}>
                <h3
                  className="title"
                  style={this.state.theme ? { color: "white" } : {}}
                >
                  {TierNames[idx]}
                </h3>
                <Row>
                  {tierArray.map((shipName, idx) => {
                    const shipInfo = require("../../assets/json/ship_info/" + shipName + ".json");
                    let inverseIndex = shipFilters.indexOf("_INVERSE");
                    let filterRole = shipFilters;
                    let display = true;
                    if (filterRole !== ""){
                      let invert = false;
                      if (inverseIndex >= 0) {
                        filterRole = shipFilters.substr(0, inverseIndex);
                        invert = true;
                      }
                      if (filterRole === "BOSS" || filterRole === "MOBS") {
                        invert = true;
                        if (filterRole === "BOSS") {
                          filterRole = "MOBS";
                        } else {
                          filterRole = "BOSS";
                        }
                      }
                      display = invert ^ shipInfo.roles.flat().includes(filterRole);
                    }
                    return (
                      <Ship
                        key={idx}
                        shipName={shipName}
                        RoleTypes={RoleTypes}
                        retrofitsList={retrofitsList}
                        openShipModal={openShipModal}
                        shipInfo={shipInfo}
                        willDisplay={display}
                        theme={this.state.theme}
                      />
                    );
                  })}
                </Row>
              </div>
            );
          } else {
            return <div key={idx}></div>;
          }
        })}
      </TabPane>
    );
  }
}
