import React from "react";
import { Card, CardBody, Container, Row, Col } from "reactstrap";
import IndexNavbar from "components/Navbars/IndexNavbar.js";
import DarkFooter from "components/Footers/DarkFooter.js";
import { Input, Button } from "semantic-ui-react";

import ShipSelector from "../components/ShipSelector/ShipSelector.js";
import { EctlCommon } from "../components/Common/EctlCommon.js";

const { createCanvas, loadImage } = require("canvas");
const ArrayBufferConcat = require('arraybuffer-concat');
const XXH = require('xxhashjs');

const Fleetmaker = () => {
  const [userFleet, setUserFleet] = React.useState({});
  const clearRef1 = React.useRef();
  const clearRef2 = React.useRef();
  const clearRef3 = React.useRef();
  const clearRef4 = React.useRef();
  const clearRef5 = React.useRef();
  const clearRef6 = React.useRef();
  const clearRef7 = React.useRef();
  const clearRef8 = React.useRef();
  const clearRef9 = React.useRef();

  const [theme] = React.useState(localStorage.getItem("theme") === "true");
  const [vgList, setVgList] = React.useState([]);
  const [mainList, setMainList] = React.useState([]);
  const [subList, setSubList] = React.useState([]);
  const [eqHashTable, setEqHashTable] = React.useState([]);
  const [bisGearTable, setBisGearTable] = React.useState([]);
  const [updateJson, setUpdateJson] = React.useState({});

  const [shareCode, setShareCode] = React.useState("");
  const [linkCopied, setLinkCopied] = React.useState(false);
  // eslint-disable-next-line no-unused-vars
  const [linkWindow, setLinkWindow] = React.useState({});

  const updateFleetJson = (val, id) => {
    if (val === null) {
      delete userFleet[id];
    } else {
      userFleet[id] = val;
    }
  };

  const createShipHullHash = () => {
    const shipListJson = require("../assets/json/ship/ship_list.json");
    let vgShipList = [{ key: 0, value: 0, text: "-" }];
    let mainShipList = [{ key: 0, value: 0, text: "-" }];
    let subShipList = [{ key: 0, value: 0, text: "-" }];

    for (let i = 0; i < shipListJson.length; i++) {
      const ship_json = require("../assets/json/ship/" +
        shipListJson[i] +
        ".json");
      const listpush = (list, typeArr) => {
        if (typeArr.includes(ship_json.Type) || typeArr.includes(ship_json.SubtypeRetro)) {
          list.push({
            key: ship_json.ShipID,
            value: list.length,
            text: ship_json.Name,
            file: shipListJson[i],
            image: {
              avatar: false,
              src: require("../assets/img/ship_images/" +
                ship_json.Name.toLowerCase() +
                "icon.png"),
            },
          });
        }
      };
      listpush(vgShipList, EctlCommon.AllHullTypes.vanguard);
      listpush(mainShipList, EctlCommon.AllHullTypes.main);
      listpush(subShipList, EctlCommon.AllHullTypes.sub);
    }

    setVgList(vgShipList);
    setMainList(mainShipList);
    setSubList(subShipList);
    return {
      vg: vgShipList,
      main: mainShipList,
      sub: subShipList,
    };
  };

  const createShipEQHash = () => {
    const eqListJson = require("../assets/json/eq_json/eq_list.json");
    let tempEqHashTable = {};
    let tempEqList = [];
    for (let i = 0; i < eqListJson.length; i++) {
      const eq_name = eqListJson[i].substr(0, eqListJson[i].length - 3);
      const eq_json = require("../assets/json/eq_json/" + eq_name + ".json");
      tempEqList.push(eq_json);
      if (tempEqHashTable[eq_json.Type] === undefined) {
        tempEqHashTable[eq_json.Type] = [];
      }
      tempEqHashTable[eq_json.Type].push({
        id: eq_json.mineid,
        value: tempEqHashTable[eq_json.Type].length,
        text: eq_json.EscapedName,
        type: eq_json.Type,
        file: eq_name,
        image: {
          avatar: false,
          src: require("../assets/img/eq_images/" + eq_json.index + ".png"),
        },
      });
    }
    setEqHashTable(tempEqHashTable);
    return tempEqList;
  };

  const copyToClipboard = (str) => {
    const el = document.createElement("textarea");
    el.value = str;
    document.body.appendChild(el);
    el.select();
    document.execCommand("copy");
    document.body.removeChild(el);
    setLinkCopied(true);
  };

  const createBigGearTable = () => {
    let json = {};
    json.mapping = require("../assets/json/gear/GearMapping.json");
    json.template = Object.assign(
      {},
      require("../assets/json/gear/BBTemplates.json"),
      require("../assets/json/gear/CATemplates.json"),
      require("../assets/json/gear/CLTemplates.json"),
      require("../assets/json/gear/CVTemplates.json"),
      require("../assets/json/gear/DDTemplates.json"),
      require("../assets/json/gear/OtherTemplates.json"),
      require("../assets/json/gear/SSTemplates.json")
    );
    json.slot = require("../assets/json/gear/SlotTemplates.json");
    setBisGearTable(json);
  };

  const compressAndMapJson = () => {
    let entries = [];
    for (let i = 0; i < 9; i++) {
      const shipEntry = 'ship' + (i + 1);
      const cacheJson = userFleet[shipEntry];
      if (!cacheJson){
        const buf = new ArrayBuffer(1);
        new DataView(buf).setUint8(0, 0);
        entries.push(buf);
        continue;
      }
      const ship_json = require('../assets/json/ship/' + cacheJson.ship + '.json');

      const shipID = ship_json.ShipID;
      let shipIDInt;
      let ns;

      if (shipID.startsWith('Collab')){
        shipIDInt = parseInt(shipID.slice('Collab'.length), 10);
        ns = 4;
      } else if (shipID.startsWith('Plan')){
        shipIDInt = parseInt(shipID.slice('Plan'.length), 10);
        ns = 3;
      } else if (isNaN(shipID)){
        // should not happen!
        console.log('shipID: ' + shipID);
        return false;
      } else {
        shipIDInt = parseInt(shipID, 10);
        ns = 2;
      }

      let control = ns;
      if (ship_json.Remodel === 't' && !cacheJson.kai){
        control |= (1 << 4);
      }

      const shipBuf = new ArrayBuffer(3);
      const shipView = new DataView(shipBuf);
      shipView.setUint8(0, control);
      shipView.setUint16(1, shipIDInt, true);
      entries.push(shipBuf);

      for (let j = 1; j <= 5; j++) {
        const eqData = cacheJson['eq' + j];
        let buf;
        if (eqData && cacheJson['eq' + j + 'save'] === true) {
          const eq_json = require('../assets/json/eq_json/' + eqData + '.json');
          const eq_value = (parseInt(eq_json.mineid, 10) << 4) + 10 + 1;
          buf = new ArrayBuffer(3);
          const view = new DataView(buf);
          view.setUint8(0, eq_value & 0xFF);
          view.setUint16(1, (eq_value >> 8) & 0xFFFF, true);
        } else {
          buf = new ArrayBuffer(1);
          new DataView(buf).setUint8(0, 0);
        }
        entries.push(buf);
      }
    }

    const buffer = ArrayBufferConcat.apply(null, entries);
    return EctlCommon.compress(buffer);
  };

  const restoredCodedJson = (inputStr, shiphash, eqlist) => {

    let buffer;
    try {
      buffer = EctlCommon.decompress(inputStr);
    } catch (error){
      console.log('decode error: ' + error);
      return false;
    }
    const view = new DataView(buffer);

    let pointer = 0;
    let shipIndex = 0;

    const restoredJson = {};

    while (pointer < buffer.byteLength){
      if (shipIndex >= 9){
        console.log('shipIndex: ' + shipIndex);
        return false;
      }
      const control = view.getUint8(pointer++);
      if (!control){
        shipIndex++;
        continue;
      }
      const ns = control & 0xF;
      let shipID = view.getUint16(pointer, true).toString().padStart(3, '0');
      pointer += 2;
      if (ns === 4){
        shipID = 'Collab' + shipID;
      } else if (ns === 3){
        shipID = 'Plan' + shipID;
      } else if (ns !== 2){
        console.log('ns: ' + ns);
        return false;
      }
      const unkai = control & 0x10;
      const hash = shipIndex < 3 ? shiphash.vg : shipIndex < 6 ? shiphash.main : shiphash.sub;
      const shipJson = hash.find(item => item.key === shipID);
      if (!shipJson){
        console.log('shipID: ' + shipID);
        return false;
      }
      const shipEntry = 'ship' + (shipIndex + 1);
      restoredJson[shipEntry] = {};
      restoredJson[shipEntry].ship = shipJson.file;
      const shipInfo = require('../assets/json/ship/' + shipJson.file + '.json');
      restoredJson[shipEntry].kai = shipInfo.Remodel === 't' && !unkai;

      for (let j = 1; j <= 5; j++){
        const eqByte = view.getUint8(pointer++);
        if (eqByte === 0){
          continue;
        }
        const eq_value = ((view.getUint16(pointer, true) << 8) | eqByte) - 1;
        pointer += 2;
        const enhance_value = eq_value & 0xF;
        // atm I'm only supporting +10 in fleetmaker
        // possible extension? maybe? maybe not?
        if (enhance_value !== 10){
          console.log('evalue: ' + enhance_value);
          return false;
        }

        const eqid = (eq_value >> 4) & 0x0FFFFF;
        const eqJson = eqlist.find(item => parseInt(item.mineid, 10) === eqid);
        if (!eqJson){
          console.log('eqid: ' + eqid);
          return false;
        }
        restoredJson[shipEntry]['eq' + j] = eqJson.index;
      }
      shipIndex++;
    }
    setUpdateJson(restoredJson);
    return true;
  };

  const getShareCodeLink = () => {
    return (
      window.location.origin +
      window.location.pathname +
      "#/fleetmaker?c=" +
      shareCode
    );
  };

  const generateFleetPicture = async () => {
    let canvasWidth = 2436;
    let canvasHeight = 0;
    let activeShip = 0;
    let activeSub = 0;

    for (let i = 0; i < 10; i++) {
      if (userFleet["ship" + i]) {
        if (i < 7) {
          activeShip++;
        } else {
          activeSub++;
        }
      }
    }

    if (activeShip > 0 && activeSub > 0) {
      canvasHeight = 1125 + 1125 + 60 + 60 + 30;
    } else {
      canvasHeight = 1125 + 60 + 30;
    }

    const canvas = createCanvas(canvasWidth, canvasHeight);
    const ctx = canvas.getContext("2d");
    ctx.imageSmoothingEnabled = false;
    ctx.mozImageSmoothingEnabled = false;

    const rainbowbg = await loadImage(
      require("../assets/img/others/rainbowbg.png")
    );
    const fleetbg = await loadImage(
      require("../assets/img/others/fleetbg.png")
    );
    const subbg = await loadImage(require("../assets/img/others/subbg.png"));

    if (activeShip > 0) {
      ctx.drawImage(fleetbg, 0, 0, 2436, 1125);

      const sizeJson = {
        eq1: {
          screen: [860, 505, 300, 300],
          icon: [30, 1125, 60, 60],
        },
        eq2: {
          screen: [1245, 430, 300, 300],
          icon: [430, 1125, 60, 60],
        },
        eq3: {
          screen: [1585, 355, 300, 300],
          icon: [830, 1125, 60, 60],
        },
        eq4: {
          screen: [640, 270, 300, 300],
          icon: [1230, 1125, 60, 60],
        },
        eq5: {
          screen: [980, 210, 300, 300],
          icon: [1630, 1125, 60, 60],
        },
        eq6: {
          screen: [1305, 155, 300, 300],
          icon: [2030, 1125, 60, 60],
        },
      };

      for (let i = 6; i > 0; i--) {
        await renderShipOnImage(
          ctx,
          rainbowbg,
          i,
          sizeJson["eq" + i].screen,
          sizeJson["eq" + i].icon
        );
      }
    }

    if (activeSub > 0) {
      if (activeShip > 0) {
        ctx.drawImage(subbg, 0, 1185, 2436, 1125);
      } else {
        ctx.drawImage(subbg, 0, 0, 2436, 1125);
      }

      const sizeJson =
        activeShip > 0
          ? {
              eq7: {
                screen: [705, 1540, 300, 300],
                icon: [339, 2310, 60, 60],
              },
              eq8: {
                screen: [1065, 1475, 300, 300],
                icon: [1038, 2310, 60, 60],
              },
              eq9: {
                screen: [1390, 1410, 300, 300],
                icon: [1737, 2310, 60, 60],
              },
            }
          : {
              eq7: {
                screen: [705, 335, 300, 300],
                icon: [339, 1125, 60, 60],
              },
              eq8: {
                screen: [1065, 270, 300, 300],
                icon: [1038, 1125, 60, 60],
              },
              eq9: {
                screen: [1390, 205, 300, 300],
                icon: [1737, 1125, 60, 60],
              },
            };

      for (let i = 9; i > 6; i--) {
        await renderShipOnImage(ctx,rainbowbg, i, sizeJson["eq" + i].screen, sizeJson["eq" + i].icon);
      }
    }

    const blob = await fetch(canvas.toDataURL()).then(r => r.blob());
    const fleetID = compressAndMapJson().replaceAll('+', '-').replaceAll('/', '_');
    const blobURL = URL.createObjectURL(blob);
    const formData = new FormData();
    formData.append('action', 'dump-check');
    formData.append('dump-location', 'ectl');
    formData.append('dump-id', fleetID);
    let hash;
    const pr = blob.arrayBuffer().then((b) => {
      hash = XXH.h64(b, 0xEC71).toString(16);
      formData.append('dump-xxhash', hash);
      return fetch('https://thebombzen.moe/api/v0/dump/', {
        method: 'POST',
        body: formData,
        mode: 'cors',
        cache: 'no-cache',
        credentials: 'omit',
        referrerPolicy: 'no-referrer',
      });
    }).then((r) => r.ok ? r.json() : {'success': false}).then((j) => {
      if (j.success){
        return j;
      } else {
        formData.set('action', 'dump');
        formData.append('dump-ext', 'png');
        formData.append('dump-time', Date.now());
        formData.append('dump-file', blob, 'dump-' + fleetID + '.png');
        return fetch('https://thebombzen.moe/api/v0/dump/', {
          method: 'POST',
          body: formData,
          mode: 'cors',
          cache: 'no-cache',
          credentials: 'omit',
          referrerPolicy: 'no-referrer',
        });
      }
    });
    return {
      'blobURL': blobURL,
      'permaURL': pr
    };
  };

  const renderShipOnImage = async (
    ctx,
    rainbowbg,
    index,
    screenImageDM,
    iconImageDM
  ) => {
    if (userFleet["ship" + index]) {
      const shipImage = await loadImage(
        userFleet["ship" + index].kai
          ? require("../assets/img/ship_images/" +
              userFleet["ship" + index].ship +
              "kaichibi.png")
          : require("../assets/img/ship_images/" +
              userFleet["ship" + index].ship +
              "chibi.png")
      );

      const projectedWidth = (shipImage.width / shipImage.height) * 150;
      let widthAdj = 2 * (Math.ceil(150 - projectedWidth) / 2);
      //let heightAdj = 2 * (Math.ceil(150 - shipImage.height) / 2);

      ctx.drawImage(
        shipImage,
        screenImageDM[0] + widthAdj,
        screenImageDM[1],
        projectedWidth * 2,
        300
      );

      const shipJson = require("../assets/json/ship/" +
        userFleet["ship" + index].ship +
        ".json");
      const rarityNum = getRarityCodedFromStr(
        shipJson.Rarity,
        userFleet["ship" + index].kai
      );
      const colorNum = getRarityColor(rarityNum);
      if (colorNum !== "ur") {
        ctx.fillStyle = colorNum;
        ctx.fillRect(
          iconImageDM[0],
          iconImageDM[1],
          iconImageDM[2],
          iconImageDM[3]
        );
      } else {
        ctx.drawImage(
          rainbowbg,
          iconImageDM[0],
          iconImageDM[1],
          iconImageDM[2],
          iconImageDM[3]
        );
      }

      let rarityNumInt = parseInt(rarityNum);
      rarityNumInt -= userFleet["ship" + index].kai ? 1 : 0;
      let adjustment = 0;
      let starCount = "5s.png";
      if (rarityNumInt < 3) {
        starCount = "4s.png";
        adjustment += 10;
      } else if (rarityNumInt > 4) {
        starCount = "6s.png";
        adjustment -= 10;
      }

      const starsIcon = await loadImage(
        require("../assets/img/others/fleetmaker/" + starCount)
      );

      const shipHullStr =
        userFleet["ship" + index].kai && shipJson.SubtypeRetro
          ? EctlCommon.getHullCodeFromStr(shipJson.SubtypeRetro)
          : EctlCommon.getHullCodeFromStr(shipJson.Type);
      const shipHullIcon = await loadImage(
        require("../assets/img/others/fleetmaker/" + shipHullStr.toLowerCase() + ".png")
      );

      ctx.drawImage(
        starsIcon,
        screenImageDM[0] + 120 + adjustment,
        screenImageDM[1] + 295,
        108,
        30
      );

      ctx.drawImage(
        shipHullIcon,
        screenImageDM[0] + 80,
        screenImageDM[1] + 322,
        191,
        37
      );

      const shipIcon = await loadImage(
        userFleet["ship" + index].kai
          ? require("../assets/img/ship_images/" +
              userFleet["ship" + index].ship +
              "kaiicon.png")
          : require("../assets/img/ship_images/" +
              userFleet["ship" + index].ship +
              "icon.png")
      );
      ctx.drawImage(
        shipIcon,
        iconImageDM[0],
        iconImageDM[1],
        iconImageDM[2],
        iconImageDM[3]
      );

      for (let j = 1; j < 6; j++) {
        const eqJson = require("../assets/json/eq_json/" +
          userFleet["ship" + index]["eq" + j] +
          ".json");

        const eqImage = await loadImage(
          require("../assets/img/eq_images/" +
            userFleet["ship" + index]["eq" + j] +
            ".png")
        );

        const colorNum = getRarityColor(eqJson.Stars);
        if (colorNum !== "ur") {
          ctx.fillStyle = colorNum;
          ctx.fillRect(
            iconImageDM[0] + j * 60,
            iconImageDM[1],
            iconImageDM[2],
            iconImageDM[3]
          );
        } else {
          ctx.drawImage(
            rainbowbg,
            iconImageDM[0] + j * 60,
            iconImageDM[1],
            iconImageDM[2],
            iconImageDM[3]
          );
        }
        ctx.drawImage(
          eqImage,
          iconImageDM[0] + j * 60,
          iconImageDM[1],
          iconImageDM[2],
          iconImageDM[3]
        );
      }
    }
  };

  const getRarityCodedFromStr = (str, iskai) => {
    let rarityNum = iskai ? 1 : 0;
    switch (str) {
      case "Normal":
        rarityNum += 2;
        break;
      case "Rare":
        rarityNum += 3;
        break;
      case "Elite":
        rarityNum += 4;
        break;
      case "Super Rare":
      case "Priority":
        rarityNum += 5;
        break;
      case "Ultra Rare":
      case "Decisive":
        rarityNum += 6;
        break;
      default:
        rarityNum += 2;
        break;
    }
    return rarityNum.toString();
  };

  const getRarityColor = (val) => {
    try {
      switch (val) {
        case "2":
          return "#ccc";
        case "3":
          return "#9fe8ff";
        case "4":
          return "#c4adff";
        case "5":
          return "#ee9";
        case "6":
          return "ur";
        default:
          return "#ccc";
      }
    } catch (e) {
      return "#ccc";
    }
  };

  React.useEffect(() => {
    document.body.classList.add("index-page");
    document.body.classList.add("sidebar-collapse");
    document.documentElement.classList.remove("nav-open");
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;

    const eqlist = createShipEQHash();
    const shipHash = createShipHullHash();
    createBigGearTable();

    const urlArr = window.location.href.split("?");
    if (urlArr.length > 1) {
      const paramArr = urlArr[1].split("&");
      for (let i = 0; i < paramArr.length; i++) {
        const paramValue = paramArr[i].split("=");
        if (paramValue[0] === "c") {
          const success = restoredCodedJson(paramValue[1], shipHash, eqlist);
          if (!success){
            alert("This restore URL is not valid.");
          }
          break;
        }
      }
    }

    return function cleanup() {
      document.body.classList.remove("index-page");
      document.body.classList.remove("sidebar-collapse");
    };

    // eslint-disable-next-line
  }, []);

  return (
    <>
      <div
        className="sub-page-background"
        style={{
          backgroundImage: "url(" + require("assets/img/bg_repeat.jpg") + ")",
          backgroundRepeat: "repeat",
          backgroundSize: "auto",
        }}
      >
        <div
          className="sub-page-background"
          style={
            theme
              ? { backgroundColor: "#000000AA" }
              : {
                  background:
                    "linear-gradient(0deg,rgba(44,44,44,.2),rgba(3,161,224,.6))",
                }
          }
        >
          <IndexNavbar isStatic={true} />
          <Container style={{ color: "#000" }}>
            <Col>
              <Card
                className="text-center"
                style={
                  theme ? { backgroundColor: "#36393F", color: "#FFFFFF" } : {}
                }
              >
                <CardBody>
                  <h2>Create Your Fleet!</h2>
                  <hr />
                  <Row>
                    <Col md="4" xs="12">
                      <Button
                        style={{ width: "100%" }}
                        color="blue"
                        onClick={() => {
                          setShareCode(compressAndMapJson());
                          setLinkCopied(false);
                        }}
                      >
                        Generate Link
                      </Button>
                    </Col>
                    <Col md="4" xs="12">
                      <div data-href="" id="gen-img-container" target="_blank" style={{'width': '100%', 'height': '100%'}}
                        onClick={async (e) => {
                          const c = document.getElementById('gen-img-container');
                          const w = window.open('about:blank', '_blank');
                          const h = document.createElement('h2');
                          h.style.textAlign = 'center';
                          h.textContent = 'One moment please...';
                          w.document.write(h.outerHTML);
                          while (!c.dataset.href){
                            await new Promise(resolve => setTimeout(resolve, 250));
                          }
                          w.location.href = c.dataset.href;
                          linkWindow[c.dataset.href] = {'w': w, 'opened': true};
                          c.dataset.href = "";
                        }}>
                        <Button
                          style={{ width: "100%" }}
                          color="blue"
                        onClick={async (e) => {
                          const c = document.getElementById('gen-img-container');
                          const url = await generateFleetPicture();
                          c.dataset.href = url.blobURL;
                          const jthen = async (j) => {
                            while (!linkWindow[url.blobURL]?.opened){
                              await new Promise(resolve => setTimeout(resolve, 250));
                            }
                            if (linkWindow[url.blobURL]?.w?.location && j.url) {
                              linkWindow[url.blobURL].w.location.href = j.url;
                            }
                            delete linkWindow[url.blobURL];
                          };
                          await url.permaURL.then((rj) => {
                            if (rj.success){
                              return jthen(rj);
                            } else if (rj.ok) {
                              return rj.json().then(jthen);
                            } else {
                               delete linkWindow[url.blobURL];
                            }
                          });
                        }}>Generate Image</Button>
                      </div>
                    </Col>
                    <Col md="4" xs="12">
                      <Button
                        style={{ width: "100%" }}
                        color="red"
                        onClick={() => {
                          if (
                            window.confirm(
                              "Are you sure? Every ship and gears will be reset."
                            )
                          ) {
                            clearRef1.current.clearShip();
                            clearRef2.current.clearShip();
                            clearRef3.current.clearShip();
                            clearRef4.current.clearShip();
                            clearRef5.current.clearShip();
                            clearRef6.current.clearShip();
                            clearRef7.current.clearShip();
                            clearRef8.current.clearShip();
                            clearRef9.current.clearShip();
                            setUpdateJson({});
                            setUserFleet({});
                          }
                        }}
                      >
                        Clear All
                      </Button>
                    </Col>
                  </Row>
                  <hr />
                  {shareCode === "" ? (
                    ""
                  ) : (
                    <Row>
                      <Col md="12" className="text-center">
                        Share this fleet:
                      </Col>
                      <Col xs="12" sm="12" md="12">
                        <Input
                          id="ship_link_tb"
                          value={getShareCodeLink(shareCode)}
                          style={{
                            width: "100%",
                            cursor: "text",
                          }}
                          placeholder="Regular"
                          type="text"
                          disabled={true}
                        ></Input>
                        <Button
                          onClick={() => {
                            copyToClipboard(getShareCodeLink(shareCode));
                          }}
                          className="btn-link"
                          color="blue"
                        >
                          {linkCopied
                            ? "Link copied!"
                            : "Click here to copy link!"}
                        </Button>
                      </Col>
                    </Row>
                  )}

                  {vgList.length === 0 ||
                  mainList.length === 0 ||
                  subList.length === 0 ||
                  eqHashTable.length === 0 ||
                  bisGearTable === {} ? (
                    "Loading.."
                  ) : (
                    <>
                      <h4>Vanguard Fleet</h4>
                      <Row>
                        <ShipSelector
                          ref={clearRef1}
                          theme={theme}
                          key="ship1"
                          selectorid="ship1"
                          shiprole="Main Tank"
                          eqlist={eqHashTable}
                          shiplist={vgList}
                          updateshipjson={updateFleetJson}
                          bisGearTable={bisGearTable}
                          updateJson={updateJson.ship1}
                        />
                        <ShipSelector
                          ref={clearRef2}
                          theme={theme}
                          key="ship2"
                          selectorid="ship2"
                          shiprole="Protected"
                          eqlist={eqHashTable}
                          shiplist={vgList}
                          updateshipjson={updateFleetJson}
                          bisGearTable={bisGearTable}
                          updateJson={updateJson.ship2}
                        />
                        <ShipSelector
                          ref={clearRef3}
                          theme={theme}
                          key="ship3"
                          selectorid="ship3"
                          shiprole="Off Tank"
                          eqlist={eqHashTable}
                          shiplist={vgList}
                          updateshipjson={updateFleetJson}
                          bisGearTable={bisGearTable}
                          updateJson={updateJson.ship3}
                        />
                      </Row>
                      <hr />
                      <h4>Main Fleet</h4>
                      <Row>
                        <ShipSelector
                          ref={clearRef4}
                          theme={theme}
                          key="ship4"
                          selectorid="ship4"
                          shiprole="Off Flag"
                          eqlist={eqHashTable}
                          shiplist={mainList}
                          updateshipjson={updateFleetJson}
                          bisGearTable={bisGearTable}
                          updateJson={updateJson.ship4}
                        />
                        <ShipSelector
                          ref={clearRef5}
                          theme={theme}
                          key="ship5"
                          selectorid="ship5"
                          shiprole="Flagship"
                          eqlist={eqHashTable}
                          shiplist={mainList}
                          updateshipjson={updateFleetJson}
                          bisGearTable={bisGearTable}
                          updateJson={updateJson.ship5}
                        />
                        <ShipSelector
                          ref={clearRef6}
                          theme={theme}
                          key="ship6"
                          selectorid="ship6"
                          shiprole="Off Flag"
                          eqlist={eqHashTable}
                          shiplist={mainList}
                          updateshipjson={updateFleetJson}
                          bisGearTable={bisGearTable}
                          updateJson={updateJson.ship6}
                        />
                      </Row>
                      <hr />
                      <h4>Submarine Fleet</h4>
                      <Row>
                        <ShipSelector
                          ref={clearRef7}
                          theme={theme}
                          key="ship7"
                          selectorid="ship7"
                          shiprole="Secondary Sub"
                          eqlist={eqHashTable}
                          shiplist={subList}
                          updateshipjson={updateFleetJson}
                          bisGearTable={bisGearTable}
                          updateJson={updateJson.ship7}
                        />
                        <ShipSelector
                          ref={clearRef8}
                          theme={theme}
                          key="ship8"
                          selectorid="ship8"
                          shiprole="Central Sub"
                          eqlist={eqHashTable}
                          shiplist={subList}
                          updateshipjson={updateFleetJson}
                          bisGearTable={bisGearTable}
                          updateJson={updateJson.ship8}
                        />
                        <ShipSelector
                          ref={clearRef9}
                          theme={theme}
                          key="key9"
                          selectorid="ship9"
                          shiprole="Secondary Sub"
                          eqlist={eqHashTable}
                          shiplist={subList}
                          updateshipjson={updateFleetJson}
                          bisGearTable={bisGearTable}
                          updateJson={updateJson.ship9}
                        />
                      </Row>
                    </>
                  )}
                </CardBody>
              </Card>
            </Col>
          </Container>
        </div>
      </div>
      <DarkFooter />
    </>
  );
};

export default Fleetmaker;
